export default "<template>\n  <div class=\"MonHocToan\">\n    <raw-loader :mytxt=\"require('!raw-loader!./BasicUpload2.vue').default\"></raw-loader>\n    <h1>UploadBasic2 page</h1>\n    <com-view-imag ref=\"listImg\"></com-view-imag>\n    <div>\n      <!-- <div>{{ds}}</div> -->\n      <img class=\"m-1\" v-for=\"x in ds\" :key=\"x.name\" :src=\"x.src\" width=\"60\" height=\"70\" />\n      <form id=\"form\" name=\"form\" action=\"/uploader\" enctype=\"multipart/form-data\" method=\"post\"\n        @submit.stop.prevent=\"onUpload\">\n        <div class=\"buttons\">\n          <div class=\"upload-button\">\n            <div class=\"label\">Click me!</div>\n            <input id=\"filessfsfsf\" name=\"files11111\" ref=\"myfile\" type=\"file\" size=\"1\" multiple @change=\"OnChange\" />\n            <button v-if=\"ds.length > 0\" type=\"submit\" class=\"mr-1\">Upload</button>\n          </div>\n        </div>\n      </form>\n\n\n    </div>\n  </div>\n</template>\n<script>\nimport $ from \"jquery\"\n//import ComViewImag from \"@/views/Upload/ComViewImage.vue\"\nimport ComViewImag from \"./ComViewImage.vue\";\nexport default {\n  name: 'Tpl',\n  components: {\n    ComViewImag\n  },\n  data() {\n    return {\n      ds: []\n    }\n  },\n  methods: {\n    onUpload() {\n      let $files = $(this.$refs.myfile);\n      let files = $files.get(0).files;\n      for (var i = 0; i < files.length; i++) {\n        let info = {\n          name: files[i].name,\n          lastModified: files[i].lastModified,\n          size: files[i].size,\n          type: files[i].type\n        }\n        //alert(JSON.stringify(info));\n      }\n      var formData = new FormData();\n      for (var i = 0; i != files.length; i++) {\n        formData.append(\"files\", files[i]);//<-------\"files\" là tham số api\n      }\n      let that = this;\n      $.ajax(\n        {\n          url: \"/api/FileUpload/Upload2\",\n          data: formData,\n          processData: false,\n          contentType: false,\n          type: \"POST\",\n          success: function (data) {\n            alert(\"Files Uploaded!\");\n            $(that.$refs.myfile).val('');\n            that.ds = [];\n            that.$refs.listImg.loadListImg();\n          }\n        }\n      );\n\n    },\n    OnChange() {\n      let $files = $(this.$refs.myfile);\n      let files = $files.get(0).files;\n      this.ds = [];\n      for (var i = 0; i < files.length; i++) {\n        let info = {\n          name: files[i].name,\n          lastModified: files[i].lastModified,\n          size: files[i].size,\n          type: files[i].type\n        }\n        this.getReadAsDataURL(files[i]).then(res => {\n          this.ds.push({\n            name: info.name,\n            src: res\n          })\n        })\n      }\n    },\n    getReadAsDataURL(file) {\n      return new Promise((resolve, reject) => {\n        let reader = new FileReader();\n        reader.onload = () => {\n          resolve(reader.result);\n        };\n        reader.onerror = reject;\n        reader.readAsDataURL(file);\n      });\n    },\n\n\n  }\n}\n</script>\n";